import React from 'react'
import ScrollDown from './ScrollDown'


export default function Question(props) {
  return (
    <div className="component__inner">
      <div className="component__title">
        <h2>Fråga {props.question.id} av {props.questionsLang}</h2>
        <hr />
      </div>
      <h1> {props.question.title} </h1>
      <span className="component__spacer" />
      <ScrollDown />
      <span className="component__spacer" />
      <div className="component__answers">
        {
          props.question.options.map((option, i) => (
            <div className="RadioButton" key={i}>
              <div className="RadioButton__outer" onClick={() => props.updateScore(option.score)}>
                <div className="RadioButton__inner"></div>
              </div>
              <div className="RadioButton__text">
                {option.option}
              </div>

              <span className="component__spacer" />
            </div>
          ))
        }
      </div>
    </div>
  )
}

